import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import { environment } from '../../environments/environment';
import { LoginPage } from '../../pages/login/login';

@Injectable()
export class AuthenticationServiceProvider {

  private token: string;
  private username: string;
  private userId: number;

  private static readonly SIGNIN_URL = environment.apiUrl + '/security/loginByGet/';
  private static readonly TOKEN_VALID_URL = environment.apiUrl + '/security/account/';
  private static readonly TOKEN_CHECK_URL = environment.apiUrl + '/security/token/checkSession/';
  private static readonly REFRESH_TOKEN_URL = environment.apiUrl + '/security/token/refresh/';

  constructor(public http: HttpClient) {
    //  public navCtrl: NavController
    // console.log('Hello AuthenticationServiceProvider Provider');
  }

  openLoginPage(){
    // this.navCtrl.setRoot(LoginPage);
  }

  checkUserSessionService() {
    const currentUser = localStorage.getItem('currentUser');
    if (currentUser != null) {
      const currentUserJson = JSON.parse(currentUser);
      if (currentUserJson.phone !== '') {
        return true;
      } else {
        localStorage.removeItem('currentUser');
        return false;
      }
    } else {
      return false;
    }
  }

  getUserToken() {
    const currentUser = localStorage.getItem('currentUser');
    if (currentUser != null) {
      const currentUserJson = JSON.parse(currentUser);

      if (currentUserJson.userToken) {
        return currentUserJson.userToken;
      } else {
        // remove user from local storage to log user out
        localStorage.removeItem('currentUser');
        this.openLoginPage();
        // this.router.navigate(['/login']);
        return '';
      }
    } else {
      // this.router.navigate(['/login']);
      // this.openLoginPage();
      return '';
      // return false;
    }
  }

  logoutService() {
    localStorage.removeItem('currentUser');
    // this.openLoginPage();
  }

  	/**
	  * Check token for given user
	  * @param username
	  * @param token
	 */
  public checkToken(username: string, token: string) {

    let urlTemp = AuthenticationServiceProvider.TOKEN_VALID_URL  + token + ".do";

    return this.http.get(urlTemp)
      .map((response: Response) => response.json())
  }

  /**
	  * Check token for given user
	  * @param username
	  * @param token
	 */
  public checkTokenData(token: string) {
    let urlTemp = AuthenticationServiceProvider.TOKEN_CHECK_URL  + token;
    return this.http.get(AuthenticationServiceProvider.TOKEN_CHECK_URL  + token + ".do")
      .map((response: Response) => {
        // let userSession = response.json() as UserSessionDataForm;
        let userSession = response.json();
        localStorage.setItem('currentUser', JSON.stringify(userSession));
      });
  } 

  /**
	  * Fetches and saves token for given user
	  * @param username
	  * @param password
	  */
   public signIn(username: string, password: string) {
    let urlTemp = AuthenticationServiceProvider.SIGNIN_URL  + username + '/' + password + ".do";
    return this.http.get(urlTemp)
    .map(res => <any>res);
    // .map((response: Response) => {
      // let userSession = response.json() as UserSessionDataForm;
      // let userSession = response.json();
      // console.log('++++++++-------' + userSession);
      // localStorage.setItem('currentUser', JSON.stringify(userSession));
      // this.reloadPage();
    // });
  }

}



