import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { environment } from '../../environments/environment';
import { AuthenticationServiceProvider } from '../authentication-service/authentication-service';

@Injectable()
export class ServicesServiceProvider {

  public static readonly GET_ALL_DETAILS = environment.apiUrl + '/mapp/api/services/all';

  constructor(public http: HttpClient, public authService: AuthenticationServiceProvider) {
    // console.log('Hello ServicesServiceProvider Provider');
  }

  getAll() {
    const userToken = this.authService.getUserToken();
    // console.log('userToken ---- ' + userToken);
    const urlToCall = ServicesServiceProvider.GET_ALL_DETAILS + '/' + userToken + '.do';

    return this.http.get(urlToCall)
    .map(res => <any[]>res);
  }

}



