import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { ContactUIForm } from '../models/contact-ui-form';
import { ContactsServiceProvider } from '../../providers/contacts-service/contacts-service';


@IonicPage()
@Component({
  selector: 'page-contact-full-details',
  templateUrl: 'contact-full-details.html',
})
export class ContactFullDetailsPage {
  selectedItem: any;
  submitAttempt: boolean = false;

  contactsForm: FormGroup;
  contactUIForm: ContactUIForm= new ContactUIForm();

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public formBuilder: FormBuilder, 
    public contactsServiceProvider: ContactsServiceProvider) {
    this.selectedItem = navParams.get('item');

    this.saveContactFormValidate();
    

  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad ContactFullDetailsPage');
  }

  saveContactForm() {
    this.submitAttempt = true;
    // Call for save

    

    if (this.contactsForm.valid) {
      console.log('validddddddd----' + this.contactUIForm);
      this.contactUIForm;
      console.log('validddddddd----' + this.contactUIForm);
      
      this.contactsServiceProvider.saveContact(this.contactUIForm).subscribe(data => {
        console.log('success');
      });

      // saveContact
      // alert('This functionality will be availabel at MISDesk CRM v2.34.0');
    } else {
      // console.log('innnnnnnnvalidddddddd----');
    }
  }  

  saveContactFormValidate() {
    const emailPattern = "^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$";

    this.contactsForm = this.formBuilder.group({
      "contactName": new FormControl(this.contactUIForm.contactName, [
        Validators.compose([Validators.maxLength(100), 
        Validators.pattern('[a-zA-Z ]*'), Validators.required])
      ]),

      "companyName": new FormControl(this.contactUIForm.company, [
        Validators.compose([
        Validators.maxLength(100), 
        Validators.pattern('[a-zA-Z ]*'), Validators.required])
      ]),

      "designationStatus": new FormControl(this.contactUIForm.designationStatus, [
        Validators.compose([Validators.maxLength(100), 
        Validators.pattern('[a-zA-Z ]*')])
      ]),

      "number1": new FormControl(this.contactUIForm.number1, [
        Validators.compose([Validators.minLength(10), Validators.maxLength(20), 
        Validators.pattern('[0-9 ]*'), Validators.required])
      ]), 

      "email1": new FormControl(this.contactUIForm.email1, [
        Validators.compose([Validators.maxLength(100), 
        Validators.pattern(emailPattern), Validators.required])
      ]),

      "number2": new FormControl(this.contactUIForm.number2, [
        Validators.compose([Validators.minLength(8), Validators.maxLength(100), 
        Validators.pattern('[0-9+ ]*')])
      ]),

      "number3": new FormControl(this.contactUIForm.number3, [
        Validators.compose([Validators.minLength(8), Validators.maxLength(100), 
        Validators.pattern('[0-9+ ]*')])
      ]),

      "contactNote": new FormControl(this.contactUIForm.contactNote, [
        Validators.compose([Validators.minLength(4), Validators.maxLength(100), 
        Validators.pattern('[a-zA-Z ]*'), Validators.required])
      ]),
        
    });

  }  


}



