import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { NotificationsServiceProvider } from '../../providers/notifications-service/notifications-service';

@IonicPage()
@Component({
  selector: 'page-notifications',
  templateUrl: 'notifications.html',
})
export class NotificationsPage {

  activeNotification= 0;

  public notificationsPOSList: any[] = [];
  public notificationsPOSSubtaskList: any[] = [];
  public notificationsTaskList: any[] = [];
  searchQuery: string = '';
  items: string[];

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public notificationsServiceProvider: NotificationsServiceProvider) {

    this.notificationsServiceProvider.getAllPOSNotifications().subscribe(data => {
      this.notificationsPOSList = data['action'];
    });
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad NotificationsPage');
  }

  getSubNotifications(item) {

    this.activeNotification = item.notificationId;
    this.notificationsServiceProvider.getByModuleIdNotificationType(
      item.notificationModuleId , item.notificationType).subscribe(data => {
      this.notificationsPOSSubtaskList = data['action'];
    });
  }

}



