import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ServicesServiceProvider } from '../../providers/services-service/services-service';

@IonicPage()
@Component({
  selector: 'page-services',
  templateUrl: 'services.html',
})
export class ServicesPage {

  public servicesList: any[] = [];
  searchQuery: string = '';
  items: string[];

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public servicesServiceProvider: ServicesServiceProvider) {

    this.servicesServiceProvider.getAll().subscribe(data => {
      this.servicesList = data;
      this.initializeItems();
    });
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad ServicesPage');
  }

  initializeItems() {
    this.items = [];
    this.servicesList.forEach(element => {
      this.items.push(element.name);
    });
  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializeItems();

    // set val to the value of the searchbar
    let val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.items = this.items.filter((item) => {
        return (item.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
  }

}



