import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
// import { HttpClient } from '@angular/common/http';
import { HttpClientModule } from '@angular/common/http';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { TasksPage } from '../pages/tasks/tasks';
import { TaskDetailsPage } from '../pages/task-details/task-details';
import { TaskDetailsSaveUpdatePage } from '../pages/task-details-save-update/task-details-save-update';
import { ContactsPage } from '../pages/contacts/contacts';
import { ContactFullDetailsPage } from '../pages/contact-full-details/contact-full-details';
import { CompaniesPage } from '../pages/companies/companies';
import { ProjectsPage } from '../pages/projects/projects';
import { OrdersPage } from '../pages/orders/orders';
import { ServicesPage } from '../pages/services/services';
import { LoginPage } from '../pages/login/login';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { AuthenticationServiceProvider } from '../providers/authentication-service/authentication-service';
import { CompaniesServiceProvider } from '../providers/companies-service/companies-service';
import { ContactsServiceProvider } from '../providers/contacts-service/contacts-service';
import { OrdersServiceProvider } from '../providers/orders-service/orders-service';
import { ProjectsServiceProvider } from '../providers/projects-service/projects-service';
import { ServicesServiceProvider } from '../providers/services-service/services-service';
import { TasksServiceProvider } from '../providers/tasks-service/tasks-service';
import { NotificationsServiceProvider } from '../providers/notifications-service/notifications-service';
import { NotificationsPage } from '../pages/notifications/notifications';

@NgModule({
  declarations: [
    MyApp,
    TasksPage,
    TaskDetailsPage,
    ContactsPage,
    CompaniesPage,
    ProjectsPage,
    OrdersPage,
    ServicesPage,
    LoginPage,
    TaskDetailsSaveUpdatePage,
    ContactFullDetailsPage,
    HomePage,
    NotificationsPage
	
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    HttpClientModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    TasksPage,
    TaskDetailsPage,
    ContactsPage,
    CompaniesPage,
    ProjectsPage,
    OrdersPage,
    ServicesPage,
    LoginPage,
    TaskDetailsSaveUpdatePage,
    ContactFullDetailsPage,
    HomePage,
    NotificationsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
	  AuthenticationServiceProvider,
    CompaniesServiceProvider,
    ContactsServiceProvider,
    OrdersServiceProvider,
    ProjectsServiceProvider,
    ServicesServiceProvider,
    TasksServiceProvider,
    NotificationsServiceProvider
  ]
})
export class AppModule {}



