import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ContactsServiceProvider } from '../../providers/contacts-service/contacts-service';
import { Contact } from '../models/contact';
import { ContactFullDetailsPage } from '../contact-full-details/contact-full-details';

/**
 * Generated class for the ContactsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-contacts',
  templateUrl: 'contacts.html',
})
export class ContactsPage {

  public contactsList: any[] = [];
  searchQuery: string = '';
  items: string[];

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public contactsServiceProvider: ContactsServiceProvider) {
      
      this.contactsServiceProvider.getAll().subscribe(data => {
        this.contactsList = data;
        this.initializeItems();
      });
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad ContactsPage');
  }

  initializeItems() {
    this.items = [];
    this.contactsList.forEach(element => {
      this.items.push(element.contactName);
    });
  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializeItems();

    // set val to the value of the searchbar
    let val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.items = this.items.filter((item) => {
        return (item.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
  }

  openContactFullDetails(item) {
    this.navCtrl.push(ContactFullDetailsPage, {
      item: item
    });
  }
}



