import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { environment } from '../../environments/environment';
import { AuthenticationServiceProvider } from '../authentication-service/authentication-service';

@Injectable()
export class NotificationsServiceProvider {

  public static readonly GET_ALL_TASKS_NOTIFICATIONS = environment.apiUrl + '/mapp/api/notification/getByUser';
  public static readonly GET_ALL_POS_NOTIFICATIONS = environment.apiUrl + '/mapp/api/notification/getPOSNotification';
  public static readonly GET_BY_MODID_NOTITYPE = environment.apiUrl + '/mapp/api/notification/getByModIdAndType';

  constructor(public http: HttpClient, public authService: AuthenticationServiceProvider) {
    // console.log('Hello NotificationsServiceProvider Provider');
  }

  getAllTasksNotifications() {
    const userToken = this.authService.getUserToken();
    const urlToCall = NotificationsServiceProvider.GET_ALL_TASKS_NOTIFICATIONS 
      + '/' + userToken
      + '/' + 0
      + '/' + 1
      + '/' + 0 + '.do';

    return this.http.get(urlToCall)
    .map(res => <any[]>res);
  }

  getAllPOSNotifications() {
    const userToken = this.authService.getUserToken();
    const urlToCall = NotificationsServiceProvider.GET_ALL_POS_NOTIFICATIONS 
      + '/' + userToken
      + '.do';

    return this.http.get(urlToCall)
    .map(res => <any[]>res);
  }  

  getByModuleIdNotificationType(moduleId, notificationType) {
    const userToken = this.authService.getUserToken();
    const urlToCall = NotificationsServiceProvider.GET_BY_MODID_NOTITYPE 
      + '/' + userToken
      + '/' + moduleId
      + '/' + notificationType
      + '.do';

    return this.http.get(urlToCall)
    .map(res => <any[]>res);
  }

}



