import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { TaskDetailsForm } from '../../pages/models/task-details-form';

import 'rxjs/add/operator/map';
import { Observable } from 'rxjs/Observable';
import { AuthenticationServiceProvider } from '../authentication-service/authentication-service';
// import 'rxjs/add/operator/map';

@Injectable()
export class TasksServiceProvider {

  public static readonly GET_ALL_DETAILS = environment.apiUrl + '/mapp/api/task/v2/getAll';
  public data: any;

  constructor(public http: HttpClient, public authService: AuthenticationServiceProvider) {
    // console.log('Hello TasksServiceProvider Provider');
  }

  // Load all github users
  load(): Observable<TaskDetailsForm[]> {

    const uid = 4;
    const cid = 0;
    const criteriaData = 'today';
    const startDateData = 'no';
    const endDateData = 'no';

    const userToken = this.authService.getUserToken();
    // console.log('userToken ---- ' + userToken);

    const urlToCall = TasksServiceProvider.GET_ALL_DETAILS
      + '/' + userToken
      + '/' + uid
      + '/' + cid
      + '/' + criteriaData
      + '/' + startDateData
      + '/' + endDateData
      + '.do';

    return this.http.get(urlToCall)
      .map(res => <any[]>res);

  }

  getAllTasksByData(uid, cid, criteriaData, startDateData, endDateData): Observable<TaskDetailsForm[]> {

    uid = 4;
    cid = 0;
    // criteriaData = 'today';
    // startDateData = 'no';
    endDateData = 'no';

    const userToken = this.authService.getUserToken();
    // console.log('userToken ---- ' + userToken);
    
    const urlToCall = TasksServiceProvider.GET_ALL_DETAILS
      + '/' + userToken
      + '/' + uid
      + '/' + cid
      + '/' + criteriaData
      + '/' + startDateData
      + '/' + endDateData
      + '.do';

    return this.http.get(urlToCall)
      .map(res => <any[]>res);

  }

}



