import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { environment } from '../../environments/environment';
import { AuthenticationServiceProvider } from '../authentication-service/authentication-service';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class CompaniesServiceProvider {

  public static readonly GET_ALL_DETAILS = environment.apiUrl + '/mapp/api/companies/all';
  
  constructor(public http: HttpClient, public authService: AuthenticationServiceProvider) { 
    // console.log('Hello CompaniesServiceProvider Provider');
  }
  
  getAll() {
    const userToken = this.authService.getUserToken();
    // console.log('userToken ---- ' + userToken);
    const urlToCall = CompaniesServiceProvider.GET_ALL_DETAILS + '/' + userToken + '.do';

    return this.http.get(urlToCall)
    .map(res => <any[]>res);
    // .catch(this.handleError);

  }

  protected handleError(error: Response): Observable<any> {
    var errorJson = error;
    if (errorJson) {
        console.error(errorJson);
        return Observable.throw('' || error);
    }
    return Observable.throw(error);
  }



}



