import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';


import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { ContactsPage } from '../contacts/contacts';
import { CompaniesPage } from '../companies/companies';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  loginModel: any = {};
  loginFormGroup: FormGroup;
  submitAttempt: boolean = false;

  constructor(public navCtrl: NavController, public navParams: NavParams, public authService: AuthenticationServiceProvider, private formBuilder: FormBuilder) {
    this.createLoginForm();
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad LoginPage');

    let checkUserSessionFlag = this.authService.checkUserSessionService();
    if (checkUserSessionFlag === true) {
      // this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
      // root out without login to dashboard

      // this.router.navigate(['/dashboard']); // TODO

    } else {
      // reset login status
      // this.authenticationService.logout();
      // this.router.navigate(['/logout']);
    }

  }

  createLoginForm() {
    this.loginFormGroup = this.formBuilder.group({
      username: ['', [Validators.required, Validators.email, Validators.minLength(5), Validators.maxLength(150)]],
      password: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(300)]],
    });
  }

  login() {
    // const userToken = this.authService.getUserToken();
    // console.log('userToken ---- ' + userToken);
    this.submitAttempt = true;
    // this.buildForm();
    if (this.loginFormGroup.valid) {
      // Show Spinner

      // santosh.v@metworld.net
      // this.openHomePage();

      // let userDataT = {"userDetailsId":3,"firstName":null,"lastName":null,"fullName":null,"userRoleId":3,"userRoleName":"GROUP COMPANY ADMIN","userId":"santosh.v@metworld.net","emailId":null,"userHomePage":null,"userToken":"XZHgswWpNSbtX8LnOEklaw==","userModelId":null};
      // localStorage.setItem('currentUser', JSON.stringify(userDataT));

      this.authService.signIn(this.loginModel.username, this.loginModel.password)
        .subscribe(
          data => {
            // console.log('++++++++-------' + data);
            localStorage.setItem('currentUser', JSON.stringify(data));
            // this.router.navigate(['/dashboard']);
            this.openHomePage();
          },
          error => {

            let cusErroJson = JSON.parse(error['_body']);
            let customizedErrorMsg = cusErroJson.message + ': ' + cusErroJson.error.message;
            if (error.status === 401) {

            } else {

            }

          });
    } else {

    }
  }

  openHomePage(){
    this.navCtrl.setRoot(CompaniesPage);
  }

}



