import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { environment } from '../../environments/environment';
import { AuthenticationServiceProvider } from '../authentication-service/authentication-service';

@Injectable()
export class OrdersServiceProvider {

  public static readonly GET_ALL_DETAILS = environment.apiUrl + '/mapp/api/orders/all';

  constructor(public http: HttpClient, public authService: AuthenticationServiceProvider) {
    // console.log('Hello OrdersServiceProvider Provider');
  }

  getAll() {
    const userToken = this.authService.getUserToken();
    // console.log('userToken ---- ' + userToken);
    const urlToCall = OrdersServiceProvider.GET_ALL_DETAILS + '/' + userToken + '.do';

    return this.http.get(urlToCall)
    .map(res => <any[]>res);
  }
}



